import React from 'react';
import classes from './Autorization.module.scss';

function Autorization() {
    return(
        <div className={classes.autorization_main}>
            <div className={classes.autorization_headline}></div>
            <input placeholder="" className={classes.autorization_input}/>
            <input placeholder="Email" className={classes.autorization_input}/>
            <input placeholder="" className={classes.autorization_input}/>
            <input placeholder="   " className={classes.autorization_input}/>
            <button className={classes.autorization_button}><div className={classes.autorization_button_text}></div></button>
        </div>
    )
}

export default Autorization;